<template>
	<!-- 顾客用户名登录页面 -->
	<view>
		<!-- 跳转至帮助页面 -->
		<view class="help">
			<text style="color: #8f8f94;margin-right:20rpx;" @click="goto_shop()">商家登录</text>
			<text style="color: #8f8f94;" @click="goto_help()">帮助</text>
		</view>

		<view class="welcome_block">
			<text class="welcome">欢迎登录小橘子</text>
		</view>
		<!-- 输入用户名和密码框 -->
		<view>
			<view class="weui-input">
				<input class="input_style" v-model="account" placeholder="请输入用户名" />
			</view>

			<view class="weui-input">
				<input class="input_style" v-model="password" :type="pwdType" placeholder="请输入密码" />
				<!-- 点击图片可显示密码是否可见 -->
				<view>
					<image :src="seen?openeye:nopeneye" @click="changeType" class="pic_pos"></image>
				</view>
			</view>

		</view>

		<view class="agree_block">
			<!-- 选择输入同意协议 -->
			<view class="choose">
				<checkbox-group @change="checkChoose">
					<label>
						<checkbox value="cb" />
					</label>
				</checkbox-group>
			</view>

			<view>
				<!-- <text class="agree">我已阅读并同意《小橘子协议》 《个人信息保护协议》</text> -->
				<div class="agree" style="display: flex;">
					<p class="agree1" style="position: relative;left: 5rpx;width: 530rpx;">
						我已阅读并同意<span>《小橘子协议》《个人信息保护协议》</span></p>
				</div>
			</view>
		</view>
		<!-- 登录按钮 -->
		<view class="btn_block">
			<button class="btn_style" @click="_check_register">登录</button>
		</view>
		<!-- 前往注册或手机号登录 -->
		<view class="flexs">
			<a class="first" @click="toRegister()">注册</a>
			<a class="second" @click="toLogin_Phone()">手机号登录</a>
		</view>

	</view>
</template>
<script>
	export default {
		data() {
			return {
				telephone: '', //顾客输入的手机号
				account: '', //顾客账号
				password: '', //顾客输入的密码
				code: '0', //用于接受后端发送的登录状态
				msg: '', //用于接受后端发送的登录信息
				exprie: '', //用于接受后端发送的登录数据
				sure: false, //用于判断用户是否同意协议 true同意 false不同意
				pwdType: "password", //判断密码是否可见的类型
				openeye: require("../../static/eye.png"), //密码可视图片地址
				nopeneye: require("../../static/no_eye.png"), //密码不可视图片地址
				seen: 0, //判断密码是否可见
				get_code: 1, //用于判断登录方式 0手机登录 1账号登录
			}
		},
		onLoad(data) {
			if (data.is_hint == 1) { //从注册页面过来的话
				//注册成功提示
				uni.showToast({
					title: "注册成功! 请登录",
					icon: 'exception',
					duration: 850
				})
				this.account = data.account
				this.password = data.password
			}
		},
		methods: {
			//页面下拉刷新后，1.5秒后停止显示下拉刷新图标
			onPullDownRefresh() {
				console.log('refresh');
				setTimeout(function() {
					uni.stopPullDownRefresh();
				}, 1500);
			},
			//前往注册界面
			toRegister() {
				uni.navigateTo({
					url: '/pages/register/register'
				})
			},
			//前往手机号登录界面
			toLogin_Phone(url) {
				uni.navigateTo({
					url: '/pages/login/login_phone'
				})
			},
			//前往注册界面
			goto_help() {
				uni.navigateTo({
					url: '/pages/help/help'
				})
			},
			//前往商家登录
			goto_shop() {
				uni.navigateTo({
					url: '/pages/login/login_shop_username'
				})
			},
			//转换密码是否可见
			changeType() {
				this.pwdType = this.pwdType === "password" ? "text" : "password";
				this.seen = !this.seen;
			},
			//用于判断用户是否同意协议 true同意 false不同意
			checkChoose: function() {
				this.sure = !this.sure;
				// console.log(this.sure);
			},
			//检验是否可以登录，不满足条件则提示信息 已满足发送请求
			_check_register() {
				if (this.account == '') {
					uni.showToast({
						title: "用户名为空",
						icon: 'error',
						duration: 850
					})
				} else if (this.password == '') {
					uni.showToast({
						title: "密码为空",
						icon: 'error',
						duration: 850
					})
				} else if (this.sure == false) {
					uni.showToast({
						title: "请先阅读并同意协议",
						icon: 'error',
						duration: 850
					})
				} else if (this.password.length < 6) {
					uni.showToast({
						title: "密码小于6位",
						icon: 'error',
						duration: 850
					})
				} else {
					//条件已满足，向/customer/customerLogin接口发送请求
					uni.request({
						url: getApp().globalData.customer_customerLogin, //仅为示例，并非真实接口地址。
						//url:'https://5t764096g4.goho.co/business/shopLogin',
						method: "POST", //不设置，默认为get方式
						data: {
							phone: this.telephone,
							account: this.account,
							password: this.password,
							code: this.get_code,
						},
						header: {},
						//登录时发送数据到数据库成功得到相应返回的数据
						success: (res) => {
							console.log("开始打印商铺用户名登录时返回的信息");
							console.log(res);

							this.code = res.data.code;
							this.msg = res.data.msg;
							getApp().globalData.token = res.data.data.Oauth_Token;
							getApp().globalData.cstm_or_sp = 1;
							getApp().globalData.login_key = true;
							console.log("开始打印登录信息");
							console.log(res);
							console.log(res.data.data.token);
							this.exprie = res.data.data.exprie;
							if (res.data.code == 1) {
								// console.log("开始跳转");
								uni.reLaunch({
									url: '/pages/my/my'
								})
							} else {
								console.log("登录错误返回信息：", this.msg)
								uni.showToast({
									title: this.msg,
									icon: 'error',
									duration: 850
								})
							}
						}
					});
				}
			}

		},
	}
</script>

<style>
	.help {
		text-align: right;
		margin-top: 30rpx;
		margin-right: 30rpx;
	}

	.welcome_block {
		margin-top: 60rpx;
		margin-left: 100rpx;
	}

	.welcome {
		font-size: 60rpx;
		font-weight: bolder;
	}

	.weui-input {
		display: flex;
		margin-left: 100rpx;
		margin-top: 70rpx;
	}

	.input_style {
		width: 540rpx;
		border-bottom-style: solid;
		border-bottom-width: 2rpx;
		border-left-width: 0rpx;
		border-top-width: 0rpx;
		border-right-width: 0rpx;
		border-bottom-color: #afafaf;
		font-size: 38rpx;
	}

	.pic_pos {
		width: 50rpx;
		height: 50rpx;
		/* 		margin-top: 60rpx;
		margin-left: 100rpx; */
	}

	.agree_block {
		display: flex;
		text-align: center;
		margin-top: 80rpx;
		width: 600rpx;
	}

	.choose {
		display: flex;
		margin-left: 100rpx;
	}

	.agree1 {
		text-align: left;
	}

	.agree span {
		color: #FFB400;
	}

	.agree2 {
		color: #FFB400;
	}

	.agree3 {
		color: #FFB400;
	}


	.flexs {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 50rpx;
	}

	.first {
		left: 0;
		margin-left: 90rpx;
		color: #8f8f94;
	}

	.second {
		right: 0;
		margin-right: 90rpx;
		color: #8f8f94;
	}

	.btn_style {
		background-color: #fefa83;
		border-radius: 20rpx;
		width: 500rpx;
		box-shadow: 4rpx 4rpx 2rpx 2rpx #8f8f94;
	}

	.btn_block {
		display: flex;
		margin-top: 120rpx;
	}

	.text_style {
		display: flex;
		text-align: center;
	}

	button::after {
		border: none;
	}
</style>
